*{
  padding: 0%;
  margin: 0%;
  box-sizing: border-box;
}
.container{
  background-color: rgb(121, 117, 117);
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  
}
.box{
  width: 800px;
  height: 400px;
  background-color: white;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  padding: 30px 10px ;
  box-shadow: 2px 2px 10px 0px black,-2px -2px 10px 0px black;
  border-radius: 15px;
  gap: 10px;
}
input{
  width: 80%;
  padding: 10px 40px;
  border-radius: 10px;
}
.search{
  display: flex;
  justify-content: center;
  gap: 20px;
  position: relative;
}
h1{
  position: absolute;
  left: 33px;
  bottom: 0px;
}

button{
  padding: 10px 20px;
  border-radius: 10px;
  background-color: black;
  color: white;
  font-weight:bolder;
}

button:hover{
  background-color: rgb(135, 138, 138);
  color: rgb(7, 7, 7);
}
.sugg{
  position: relative;
  left: 30px;
  width: 500px;
  overflow: hidden;
  cursor: pointer;
  }
  .suggestion:hover{
    background-color: rgba(103, 105, 105,0.5);
    border-radius: 10px;
    
     }
  .suggestion{
  
    display: flex;
    padding: 1px 15px;
 
  }
@media (320px <= width <= 600px)  {
.box{
  width: 300px;
  position: relative;
}
h1{
  position:absolute;
  left: 10px;
}
.search{
  display: flex;
  gap: 10px;

}
input{
  width: 180px;
  padding-left: 40px;
}
.sugg{
  width: 240px;
}
}
@media (600px <= width <= 920px){
  .box{
    width: 580px;
    position: relative;
  }
  h1{
    position: absolute;
    left: 10px;
  }
  
}